@import '../../utils/mixins';

.search_bar {
  display: flex;
  flex-direction: column;
  width: min-content;
  margin: auto;
  margin-bottom: 20px;
}

.search_container {
  display: flex;
  flex-direction: column;
  gap: 20px;

  @include onTablet {
    flex-direction: row;
    justify-content: space-between;
  }
}

.input {
  font-family: 'Mochiy Pop One', sans-serif;
  color: gray;
  padding: 8px 0 12px 12px;
  border: 1px solid green;
  border-radius: 15px;
  outline: none;

  &:focus {
    border: 1px solid rgb(0, 220, 0);
  }
}

.button {
  font-family: 'Mochiy Pop One', sans-serif;
  padding: 0px 5px 3px 5px;
  height: 45px;
  border: 1px solid salmon;
  background-color: #fff;
  color: salmon;
  border-radius: 15px;
  
  &:hover {
    cursor: pointer;
    background-color: salmon;
    color: #fff;
  }

  &:active {
    border: 1px solid green;
    background-color: #fff;
    color: gray;
  }
}